<template>
    <div id="Componente2" class="container">
        <div id="listaJuegos">
            <table class="table table-hover table-bordered">
                <thead>
                    <tr class="table-secondary">
                        <th>#</th>
                        <th>Juego</th>
                        <th>Eliminar juego de tu lista</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="table-light" v-for="(juego, index) in juegos" v-bind:key="juego">
                        <td>{{ index }}</td>
                        <td>{{ juego }}</td>
                        <td><button class="btn btn-danger btn-sm" v-on:click="eliminar(index)">Eliminar</button></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div>
            <form>
                <label for="nombreJuego" class="form-label"><b>Ingrese un juego</b></label>
                <input class="form-control" type="text" id="nombreJuego" v-model="nuevoJuego">
                <button class="btn btn-info mt-3" v-on:click.prevent="agregar">Enviar</button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Componente2',
    props: {
        juegos: {
            type: Array,
            required: true,
        }
    },
    data: function(){
    return { 
        nuevoJuego:"",
        }
    },
    methods: {
        agregar: function(){
            this.$emit('add', this.nuevoJuego);
            this.nuevoJuego = '';
        },
        eliminar: function(index){
            this.$emit('delete', index);
        }
    }
}
</script>

<style scoped>
#Componente2 {
    display: flex;
    justify-content: space-around;
    border: 1px solid fuchsia;
    border-radius: 5px;
    background-color: rgb(252, 202, 252);
    margin-top: 30px;
    padding: 2%;
}
#listaJuegos {
    display: flex;
    flex-direction: column;
}
.contenedorJuegos {
    display: flex;
    flex-direction: row;
    margin: 15px;
    align-items: baseline;
}
p {
    font-weight: bold;
    margin-right: 10px;
}
</style>