<template>
    <div id="Componente3" class="container">
        <div id="contenedor2">
            <div id="listaFotos">
                <div class="contenedorFotos">
                    <img v-for="(foto, index) in fotos" v-bind:key="foto" v-bind:src="foto" :alt="index" v-on:click="abrirImg">
                </div>
            </div>
            <div v-if="hayClick" id="descripcion">
                <div id="divImg">
                    <img v-bind:src="imagenSeleccionada" alt="">
                    <p>Autor: {{ autorSeleccionado }}</p>
                    <button class="btn btn-dark btn-sm" v-on:click="cerrarDescripcion">Cerrar</button>
                </div>
                <div id="divTxt">
                    <p>{{ descripcionSeleccionada }}</p>
                </div>
            </div>
        </div>
        <div class="mt-5">
        <hr>
        <h3 class="text-start mb-3">Agregar imagen:</h3>
        <form action="" class="mt-3">
            <div class="mb-3">
                <label for="url" class="form-label">URL imagen:</label>
                <input type="text" class="form-control" id="url" v-model="url">
            </div>
            <div class="mb-3">
                <label for="autor" class="form-label">Autor:</label>
                <input type="text" class="form-control" id="autor" v-model="autor">
            </div>
            <div class="mb-3 txtarea">
                <label for="descripcion" class="form-label">Descripción:</label>
                <textarea class="form-control" id="descripcion" cols="35" rows="3" v-model="descripcion"></textarea>
            </div>
            <div class="divbtn">
            <button @click.prevent="enviarImg" class="btn btn-success">Enviar</button>
            </div>
        </form>
    </div>
    </div>
</template>

<script>
export default {
    name: 'Componente3',
    props: {
        fotos: {
            type: Array,
            required: true,
        },
        descripciones: {
            type: Array
        },
        autores: {
            type: Array
        }
    },
    data: function() {
        return {
            hayClick: false,
            imagenSeleccionada: '',
            descripcionSeleccionada: '',
            autorSeleccionado: '', 
            url: '',
            autor: '',
            descripcion: ''
        }
    },
    methods: {
        abrirImg: function(event) {
            this.hayClick = true;
            let indiceImg = Number(event.target.alt);
            this.imagenSeleccionada = this.fotos[indiceImg];
            this.descripcionSeleccionada = this.descripciones[indiceImg];
            this.autorSeleccionado = this.autores[indiceImg];
        },
        cerrarDescripcion: function() {
            this.hayClick = false;
        },
        enviarImg: function() {
            this.$emit('agregarImagen', this.url);
            this.$emit('agregarAutor', this.autor);
            this.$emit('agregarDescripcion', this.descripcion);
            this.url = ''
            this.autor = ''
            this.descripcion = ''
        }
    }
}
</script>

<style scoped>
#Componente3 {
    border: 1px solid rgb(224, 67, 67);
    border-radius: 5px;
    background-color: rgb(241, 170, 170);
    margin-top: 30px;
    padding: 2%;
    margin-bottom: 30px;
}
#contenedor2 {
    display: flex;
    justify-content: flex-start;
}
.contenedorFotos {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    border: 1px solid blue;
    border-radius: 5px;
    background-color: aliceblue;
    width: 100%;
    height: 300px;
    overflow: scroll;
}
img {
    width: 60%;
    margin: 5px;
}
#listaFotos {
    width: 30%;
}
#descripcion {
    width: 70%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
#divImg {
    flex: 60%;
}
#divTxt {
    flex: 20%;
}
#divTxt p {
    color: brown;
}
form {
    display: flex;
    justify-content: space-between;
}
.divbtn {
    display: flex;
    align-items: center;
}
.txtarea {
    display: flex;
    flex-direction: column;
    align-items: center;
}

</style>