<template>
  <div id="app">
    <Componente1 msg="Ingrese sus datos"></Componente1>
    <Componente2
      v-bind:juegos = "arregloJuegos"
      v-on:add ="agregarJuego"
      v-on:delete="eliminarJuego"></Componente2>
    <Componente3
    v-bind:fotos = "arreglofotos"
    v-bind:descripciones = "arregloDescripciones"
    v-bind:autores = "arregloAutores"
    v-on:agregarImagen = "incluirArrayFotos"
    v-on:agregarAutor = "incluirArrayAutores"
    v-on:agregarDescripcion = "incluirArrayDescripciones">
    </Componente3>
  </div>
</template>

<script>
import Componente1 from './components/Componente1.vue';
import Componente2 from './components/Componente2.vue';
import Componente3 from './components/Componente3.vue';

export default {
  name: 'App',
  components: {
    Componente1,
    Componente2,
    Componente3
  },
  data: function(){
    return{
      arregloJuegos: ['Arena of Valor', 'Pokemon Unite', 'Donkey Kong', 'Heroes of the Storm'],
      arreglofotos: [
        'https://www.tiendanimal.es/articulos/wp-content/uploads/2017/10/enfermedades-conejos-1200x900.jpg',
        'https://verdecora.es/blog/wp-content/uploads/2021/04/como-es-conejo.jpg.webp',
        'https://okdiario.com/img/2022/04/07/conejo-655x368.jpg'
      ],
      arregloDescripciones: [
        'Conejo de color cafe claro en un jardín',
        'Conejo de color cafe claro en un bosque',
        'Conejo de color cafe claro en un cerro'
      ],
      arregloAutores: [
        'Desconocido',
        'Desconocido',
        'Desconocido'
      ]
    }
  },
  methods:{
    agregarJuego: function(nombre){
      this.arregloJuegos.push(nombre);

    },
    eliminarJuego: function(index){
      this.arregloJuegos.splice(index, 1);
    },
    incluirArrayFotos: function(src) {
      this.arreglofotos.push(src);
    },
    incluirArrayAutores: function(nombreAutor) {
      this.arregloAutores.push(nombreAutor);
    },
    incluirArrayDescripciones: function(texto) {
      this.arregloDescripciones.push(texto)
    }
  }
}
</script>

