<template>
    <div id="Componente1" class="container">
      <div id="form">
        <h2>{{ msg }}</h2>
        <form class="text-start">
          <div class="mb-3">
            <label for="nombre" class="form-label">Nombre</label>
            <input type="text" class="form-control" id="nombre" v-model="nombre">
          </div>
          <div class="mb-3">
            <label for="apellido" class="form-label">Apellido</label>
            <input type="text" class="form-control" id="apellido" v-model="apellido">
          </div>
          <div class="mb-3">
            <label for="correo" class="form-label">Correo electrónico</label>
            <input type="email" class="form-control" id="correo" aria-describedby="emailHelp" v-model="correo">
          </div>
          <div class="mb-3">
            <label for="edad" class="form-label">Edad</label>
            <input type="number" class="form-control mb-3" id="edad" v-model="edad">
          </div>
          <p v-if="edad > 0 && edad <= 13">Etapa: niñez</p>
          <p v-else-if="edad >= 14 && edad <= 17">Etapa: adolescencia</p>
          <p v-else-if="edad >= 18 && edad <= 35">Etapa: adulto joven</p>
          <p v-else-if="edad >= 36 && edad <= 64">Etapa: adulto</p>
          <p v-else-if="edad >= 65">Etapa: tercera edad</p>
          <button @click="enviarDatos" type="button" class="btn btn-primary">Enviar</button>
        </form>
      </div>
      <div id="salida">
        <h3> {{ saludo }}</h3>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Componente1',
  props: {
    msg: String
  },
  data: function(){
    return { 
      nombre:"",
      apellido: "",
      correo: "",
      edad: "",
      saludo: "",
    }
  },
  methods: {
    enviarDatos: function() {
      this.saludo = `Saludos ${this.nombre} ${this.apellido} tu correo es ${this.correo}`
    }
  }
}
</script>

